@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f5f5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}.language-selector-popper-container{z-index:10}.language-selector-popper{padding:15px}.App-link{color:#61dafb}.report-problem-container{display:block;position:fixed;bottom:0;left:0;width:100%;background-color:#fff;padding:15px 10%;box-sizing:border-box;text-align:left;border-radius:0;z-index:10}.report-link{float:right}.std-accordian{overflow:hidden}.std-accordian .MuiAccordionSummary-root{background-color:#f3f3f3}.std-accordian .std-textarea{width:100%;border-radius:5px;font-family:monospace}.header .logo{height:40px;display:inline-block;vertical-align:middle;margin-right:10px}.header .logo svg{height:100%;width:auto}@media all and (max-width: 820px){.header .title-desktop{display:none}}@media all and (min-width: 820px){.header .title-mobile{display:none}}@media all and (max-width: 820px){.header .language-toggle-text{display:none}}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@media all and (min-width: 800px){.mobile{display:none !important}}@media all and (max-width: 800px){.desktop{display:none !important}.report-problem-container{font-size:12px;padding:10px}.report-link{float:none;display:block}}
